form {
    background-color: rgba(234, 240, 248, 0.7);
    border-radius: 10px;
    padding: 22px;
}

.inlineWrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
} 

.input_addTask {
    border-radius: 3px;
    border: none;
    font-size: 22px;
    padding: 5px;
}

.priority {
    margin-left: 10px;
    display: flex;
    align-items: center;
    font-size: 20px;
}

button {
    display: block;
    margin: auto;
   border: none;
    padding: 10px;
    border-radius: 5px;
    background-color: rgb(27, 87, 160);
    width: 100px;
    font-weight: bold;
    color: rgb(233, 242, 250);
    cursor: pointer;
}
 
@media screen and (max-width: 375px) {
    form {
        padding: 20px;
    }
    .inlineWrapper {
        flex-direction: column;
    }
   .priority {
    margin-top: 12px;
    font-size: 20px;
   }
}